<template>
  <div>
    <el-table
      :data="resumeList"
      v-if="departmentId"
      style="width: 100%">
      <el-table-column
        prop="currentStatus.value"
        label="">
      </el-table-column>
      <el-table-column
        prop="username"
        label="">
      </el-table-column>
      <el-table-column
        prop="job.name"
        label="">
      </el-table-column>
      <el-table-column
        prop="form.value"
        label="">
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID">
      </el-table-column>
      <el-table-column
        prop="mailSize"
        label="(kb)">
      </el-table-column>
      <el-table-column
        prop="integral"
        label="">
      </el-table-column>
      <el-table-column
        prop="operationUser.name"
        label="">
      </el-table-column>
      <el-table-column
        prop="operationTime"
        label="">
        <template slot-scope="scope">
          {{getTimeFormat(scope.row.operationTime)}}
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="380">
        <template slot-scope="scope">
          <button class="com-btn com-detail" @click="handleDetail(scope.row, scope.column, scope.$index)"></button>
          <button class="com-btn com-edit" @click="handleEmailDetail(scope.row)"></button>
          <button class="com-btn com-history" @click="handleHistory(scope.row)"></button>
          <button class="com-btn com-op" @click="handleOperate(scope.row)"
                  v-if="user.roles.some(function(role) {
        return role.id === rolesCpt.admin || role.id === rolesCpt.reExam
      })"></button>
        </template>
      </el-table-column>
    </el-table>

    <div class="com-page-box-dialog">
      <div class="com-pages">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="resumeListTotal" v-if="resumeListTotal > 10">
        </el-pagination>
      </div>
    </div>

    <div v-if="!departmentId" style="padding: 30px 50px;text-align: center;border-bottom: 1px dashed #ccc;color: #888;">
      
    </div>

    <!---->
    <com-dialog
      :show="detailDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">:</span>{{resumeData.processedStatus}}</p>
        <p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{resumeData.name}}</p>
        <p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{resumeData.pos}}</p>
        <p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{resumeData.from}}</p>
        <p><span
          class="label">I&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;D:</span>{{resumeData.id}}
        </p>
        <p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{resumeData.size}}kb</p>
        <p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{resumeData.integral}}</p>
        <p><span class="label">:</span>{{resumeData.processedUser}}</p>
        <p><span class="label">:</span>{{resumeData.processedTime}}</p>

        <div v-if="resumeData.currentStatusId === 35">
          <p><span class="label">:</span></p>
        </div>
        <div v-if="resumeData.currentStatusId === 23">
          <p><span class="label">:</span></p>
        </div>
        <div v-if="resumeData.currentStatusId === 24">
          <p><span class="label">:</span></p>
        </div>
      </div>

      <div slot="title" class="com-dialog-title t-1">
        
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="closeDialog">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="emailDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{resumeData2.mailTitle}}</p>
        <!--<p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{getTimeFormat(resumeData2.mailSendTime)}}</p>-->
        <p><span class="label">&nbsp;&nbsp;&nbsp;&nbsp;:</span>{{resumeData2.mailId}}</p>

        <p class="com-dashed-line"></p>

        <p><span class="label label-block">
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>
          <a target="_blank" :href="'http://192.168.1.158:8080/oa' + resumeData2.mailSavePath"></a></p>

        <p class="com-dashed-line"></p>

        <p>
          <span class="label label-block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>
          <span class="label-block" v-for="f in resumeData.enclosures">{{f.name}}&nbsp;&nbsp;<a target="_blank" :href="serverBaseUrlCpt + f.path"></a></span>
        </p>
      </div>

      <div slot="title" class="com-dialog-title t-2">
        
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #FEC71E; border-color: #FEC71E;" type="primary"
                   @click="closeDialog">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="historyStatusDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <el-table
        :data="resumeHistoryList"
        style="width: 100%">
        <el-table-column
          prop="processTime"
          label="">
          <template slot-scope="scope">
            {{getTimeFormat(scope.row.operationTime)}}
          </template>
        </el-table-column>

        <el-table-column
          prop="statusSmall.value"
          label="">
        </el-table-column>

        <el-table-column
          prop="resume.id"
          label="ID">
        </el-table-column>

        <el-table-column
          prop="operationUser.name"
          label="">
        </el-table-column>

      </el-table>

      <!--<div class="com-page-box-dialog">-->
      <!--<div class="com-pages">-->
      <!--<el-pagination-->
      <!--@size-change="handleSizeChange"-->
      <!--@current-change="handleCurrentChange"-->
      <!--:current-page="currentPage"-->
      <!--:page-sizes="pageSizes"-->
      <!--:page-size="pageSize"-->
      <!--layout="total, sizes, prev, pager, next, jumper"-->
      <!--:total="resumeListTotal">-->
      <!--</el-pagination>-->
      <!--</div>-->
      <!--</div>-->

      <div slot="title" class="com-dialog-title t-3">
        
        <!--<div class="close x-color-3" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #5AD1D1; border-color: #5AD1D1;" type="primary"
                   @click="closeDialog">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="operateDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div>
        <ul class="op-sel">
          <li @click="opSelect(0)" :class="{active: isActive[0]}">() <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(1)" :class="{active: isActive[1]}">() <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(2)" :class="{active: isActive[2]}"> <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(3)" :class="{active: isActive[3]}"> <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(4)" :class="{active: isActive[4]}"> <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(5)" :class="{active: isActive[5]}"> <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(6)" :class="{active: isActive[6]}"> <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(7)" :class="{active: isActive[7]}">() <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(8)" :class="{active: isActive[8]}"> <i class="sprites icon-hook"></i></li>
        </ul>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="opHandle">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="changeReTestTimeDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div class="test-form">
        <p>
          <label></label>
          <el-date-picker
            v-model="initTestForm.time"
            type="datetime"
            placeholder="">
          </el-date-picker>
        </p>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="saveOperate">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="screenDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div class="test-form">
        <p>
          <label></label>
          <el-input
            type="textarea"
            :rows="6"
            placeholder=""
            v-model="initTestForm.msg">
          </el-input>
        </p>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="saveOperate">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="notReachedDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div class="test-form">
        <p>
          <label></label>
          <el-date-picker
            v-model="initTestForm.time"
            type="datetime"
            placeholder="">
          </el-date-picker>
        </p>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="saveOperate">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="entryDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div class="test-form">
        <p>
          <label style="margin-bottom: 5px"></label>
          <el-date-picker
            v-model="initTestForm.time"
            type="datetime"
            placeholder="">
          </el-date-picker>
        </p>
        <p>
          <label style="margin-bottom: 5px"></label>
          <el-input
            type="number"
            :rows="6"
            placeholder=""
            v-model="initTestForm.msg">
          </el-input>
        </p>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="saveOperate">
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import { formatDateTime } from '../../../../utils/utils'
  import comDialog from '../../../../components/toast/dialog.vue'
  import { mapState, mapActions } from 'vuex'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 
        pageSizes: [10, 20],
        currentStatusId: 14,
        resumeData: {
          processedStatus: '',
          name: '',
          pos: '',
          from: '',
          id: '',
          size: '',
          integral: '',
          processedUser: '',
          processedTime: ''
        },
        resumeData2: {
          resume: {
            mailSendTime: 0
          }
        },
        detailDialogVisible: false,
        emailDialogVisible: false,
        historyStatusDialogVisible: false,
        operateDialogVisible: false,
        isActive: [false, false, false, false, false, false, false, false, false],
        opStatusId: 0, // id
        initTestForm: {
          time: '',
          msg: '',
          print: [],
        },
        changeReTestTimeDialogVisible: false, // 
        screenDialogVisible: false, // 
        notReachedDialogVisible: false, // 
        entryDialogVisible: false, // 
      }
    },
    computed: {
      ...mapState('resume', [
        'resumeList',
        'resumeListTotal',
        'resumeHistoryList',
        'operationRecordAddStatus',
      ]),
      ...mapState([
        'departmentId',
        'searchValue',
        'user',
      ]),
    },
    watch: {
      departmentId () {
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            resume: {job: {department: {id: this.departmentId}}}
          },
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      searchValue (d) {
        if (d) {
          this.getResumeList()
        }
      },
      operationRecordAddStatus (d) {
        if (d.status) {
          this.getResumeList()
          this.closeDialog()
        }
      },
    },
    methods: {
      ...mapActions('resume', [
        'ac_resumeList',
        'ac_resumeHistoryList',
        'ac_OperationRecordAdd',
      ]),
      handleDetail (row) {
        this.detailDialogVisible = true
        this.resumeData = {
          processedStatus: row.currentStatus.value,
          name: row.username,
          pos: row.job.name,
          from: row.form.value,
          id: row.id,
          size: row.mailSize,
          integral: row.integral,
          processedUser: row.operationUser.name,
          processedTime: this.getTimeFormat(row.operationTime),
          currentStatusId: row.currentStatus.id,
          remarks: row.remarks,
          phone: row.phone
        }
      },
      handleEmailDetail (row) {
        this.emailDialogVisible = true
        this.resumeData2 = row
      },
      handleHistory (row) {
        this.historyStatusDialogVisible = true
        this.ac_resumeHistoryList({
          resume: {id: row.id}
        })
      },
      handleOperate (row) {
        this.operateDialogVisible = true
        this.resumeData = row
        this.initTestForm.msg = row.phone
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.getResumeList()
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.getResumeList()
      },
      getTimeFormat (timeStamp) {
        return formatDateTime(timeStamp)
      },
      closeDialog () {
        this.detailDialogVisible = false
        this.emailDialogVisible = false
        this.historyStatusDialogVisible = false
        this.operateDialogVisible = false
        this.resumeData = {
          processedStatus: '',
          name: '',
          pos: '',
          from: '',
          id: '',
          size: '',
          integral: '',
          processedUser: '',
          processedTime: ''
        }
        this.initTestForm = {
          time: '',
          msg: '',
          print: [],
        }
        this.changeReTestTimeDialogVisible = false // 
        this.screenDialogVisible = false // 
        this.notReachedDialogVisible = false // 
        this.entryDialogVisible = false // 
        this.opStatusId = 0
        this.isActive = [false, false, false, false, false, false, false, false, false]
      },
      opHandle () {
        if (this.opStatusId) {
          this.saveOperate()
        }
      },
      opSelect (index) {
        let arr = [false, false, false, false, false, false, false, false, false]
        arr[index] = true
        this.isActive = arr
        switch (index) {
          case 0:
            this.opStatusId = 19 // 
//            this.saveOperate()
            break
          case 1:
            this.opStatusId = 14 // ()
            this.operateDialogVisible = false
            this.changeReTestTimeDialogVisible = true
            break
          case 2:
            this.opStatusId = 21 // 
            this.operateDialogVisible = false
            this.screenDialogVisible = true // 
            break
          case 3:
            this.opStatusId = 22 // 
//            this.saveOperate()
            break
          case 4:
            this.opStatusId = 23 // 
//            this.saveOperate()
            break
          case 5:
            this.opStatusId = 24 // 
            this.operateDialogVisible = false
            this.notReachedDialogVisible = true // 
            break
          case 6:
            this.opStatusId = 20 // 
            this.operateDialogVisible = false
            this.entryDialogVisible = true // 
            break
          case 7:
            this.opStatusId = 33 // 
//            this.saveOperate()
            break
          case 8:
            this.opStatusId = 25 // 
//            this.saveOperate()
            break
          default:
        }
      },
      saveOperate () {
        let param = {
          resume: {
            id: this.resumeData.id
          },
          statusSmall: {
            id: this.opStatusId
          }
        }
        if (this.initTestForm.time) {
          param.bespeakTime = Date.parse(this.initTestForm.time)
        }
        if (this.initTestForm.msg) {
          param.phone = this.initTestForm.msg
        }
        this.ac_OperationRecordAdd(param)
      },
      getResumeList () {
        if (this.searchValue) {
          this.ac_resumeList({
            entity: {
              statusBig: {id: this.currentStatusId},
              resume: {job: {department: {id: this.departmentId}}},
              key: this.searchValue
            },
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        } else {
          this.ac_resumeList({
            entity: {
              statusBig: {id: this.currentStatusId},
              resume: {job: {department: {id: this.departmentId}}},
            },
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        }
      }
    },
    components: {
      comDialog,
    },
    beforeCreate () {},
    created () {
      if (this.departmentId) {
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            resume: {job: {department: {id: this.departmentId}}},
          },
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      }
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../../styles/common";
  @import "../../../../styles/sprites/sprites.css";

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .t-2 {
    background-color: #FDC61E;
  }

  .x-color-2 {
    color: #FDC61E;
  }

  .t-3 {
    background-color: #5AD1D1;
  }

  .x-color-3 {
    color: #5AD1D1;
  }

  .t-4 {
    background-color: #EB6941;
  }

  .x-color-4 {
    color: #EB6941;
  }

  .resume-detail {
    p {
      line-height: 40px;
    }
    .label {
      margin-right: 20px;
      &.label-block {
        display: block;
      }
    }
  }

  .op-sel {
    li {
      border-radius: 5px;
      background-color: #EFEFEF;
      margin-bottom: 20px;
      padding: 8px 20px;
      color: #646464;
      font-size: 12px;
      cursor: pointer;
      position: relative;
      i {
        position: absolute;
        right: 20px;
        display: none;
      }
      &.active {
        background-color: #4294E7;
        color: #fff;
        i {
          display: block;
        }
      }
    }
  }

  .test-form {
    p {
      padding: 10px;
    }
  }
</style>



